import config from '../../components/config/config.vue'
export default {
	data() {
		return {
			nickname:"",
			user_name:"",
			phone:"",
			password:"",
			password_confirm:"",
		}
	},
	methods: {
		txtshow(mess){
			uni.showToast({
				icon:"none",
			    title: mess,
			    duration: 2000
			});
		},
		submitregist(){
			if(this.nickname.length>=2&&this.nickname.length<=30){
				if(this.user_name.length>=5&&this.nickname.length<=30){
					if((/^1[3456789]\d{9}$/.test(this.phone))){
						if(this.password.length>=8&&this.password.length<=40){
							if(this.password==this.password_confirm){
								//提交注册
								uni.request({
								    url: config.httpUrl +'/api/v1/user/register',
								    data: {
										'nickname':this.nickname,
								        'user_name':this.user_name,
										'phone':this.phone,
										'password':this.password,
										'password_confirm':this.password_confirm,
								    },
									method:"POST",
								    header: {
								       'content-type': 'application/x-www-form-urlencoded',
								    },
								    success: (res) => {
										var jsondata=res.data
										if(jsondata.code=='0'){//登录成功
											//console.log(res)															
										this.txtshow('注册成功')
										setTimeout(function(){
												uni.redirectTo({
												url: 'login'
												});				
											},2000)										
										}else{
											this.txtshow(jsondata.msg)
										}								
								    },
									fail: () =>{  
										this.txtshow('网络异常')
								     }, 
								});
							}else{
								this.txtshow("两次密码不一致")
							}
						}else{
							this.txtshow("密码长度8-40之间")
						}
					}else{
						this.txtshow("手机号不正确")
					}
				}else{
					this.txtshow("用户名长度5-30之间")
				}
			}else{
				this.txtshow("昵称长度2-30之间")
			}
		},
		register(){
			uni.redirectTo({
			    url: 'login'
			});
		}
	},
	onLoad() {
		
	},
	onReady() {
		
	}
}
